.intro {   
    background-color: #F1F2F4;
    padding: 50px 0;
    position: relative;   
    margin-bottom: 130px;   

    .container {        
        min-height: 617px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    &__navigation {
        display: flex;
        justify-content: space-between;
    }

    &__list {
        margin: 0;
        padding: 0;
        list-style: none;   
    }

    &__item {
        margin-bottom: 35px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__back-link {
        display: inline-block;
        font-family: $font-family-primary;
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 21px;        
        text-decoration: none;
        color: #151C22;
        margin-left: 10px;
        margin-right: 0;        
    }

    &__back-link[href] {
        color: #727A82;
        margin-right: 10px;

        &:first-child {
            margin-left: 0;
        }
    }

    &__link {
        font-family: $font-family-primary;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #151C22;     
        text-decoration: none;  

        svg {
            fill: transparent;
        }
    }

    &__main {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;  
        margin: auto;     
        max-width: 831px;      
        display: flex;
        justify-content: space-between;
    }

    &__intro {
        padding-top: 192px;
    }

    &__img {
        position: relative;      
        margin-top: 50px;

        img {
            position: relative;
            z-index: 1;
        }

        &::before {        
            content: "";
            position: absolute;
            top: -50px;
            left: -50px;
            width: 232px;
            height: 176px;
            background-image: url("../img/diagonal-left.svg");
        }

        &::after {
            content: "";
            position: absolute;
            bottom: -50px;
            right: -50px;
            width: 232px;
            height: 176px;
            background-image: url("../img/diagonal-right.svg");
        }
    }

    &__text {
        font-family: $font-family-primary;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 21px;
        display: flex;
        align-items: center;
        color: #727A82;
        margin: 0 0 25px 0;
    }

    &__title {
        font-family: $font-family-secondary;
        font-style: normal;
        font-weight: normal;
        font-size: 128px;
        line-height: 1; 
        color: #151C22;
        margin: 0 0 50px 0;
    }

    &__btn {
        max-width: 219px;
    }

    &--catalog {
        padding-top: 0;
        padding-bottom: 0;

        .container {
            flex-direction: row;           
            min-height: initial;
            max-width: 760px;
        }
        
        .intro__title {            
            font-family:  $font-family-primary;
            font-style: normal;
            font-weight: 700;
            font-size: 32px;
            line-height: 125%;  
            max-width: 272px;    
            margin: 0; 
        }

        .intro__intro {
            padding-top: 73px;
        }

        .intro__img {
            margin: 0;

            img {
                height: 100%;
            }

            &::before {      
              top: initial;
              bottom: 0;
              left: -30px;      
            }

            &::after {      
                bottom: initial;
                top: 0;
                right: -30px;
                background-image: url("../img/diagonal-left.svg");
            }
        }
    }
}